@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  padding: 0;
  margin: 0;
}
.App {
  font-family: "Roboto", sans-serif;
  text-align: center;
  background-color: #005b96;
  overflow-x: hidden;
}
nav {
  height: 3rem;
}
li {
  list-style: none;
  padding: 0 10px;
  background-color: gray;
  border-radius: 10px;
  display: inline-block;
}
li:hover {
  background-color: #e2e2e2;
}
.link {
  color: #fff;
  text-decoration: none;
  font-style: none;
}
.link:hover {
  color: #fff;
}

.coin-input {
  border-radius: 15px;
  padding: 0 15px;
}
.coin-input:focus {
  outline: none;
}
.crypto {
  min-height: 100vh;
}
.btn {
  margin: 20px 0px;
  border-radius: 20px;
}

.chart {
  width: 100%;
}

.card-img {
  width: 200px;
}
.card {
  border-radius: 10px;
}
.card:hover {
  background-color: #e2e2e2;
}

